@import url(https://fonts.googleapis.com/css2?family=Rubik:wght@400;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{padding:0;margin:0;box-sizing:border-box}body{font-family:'Rubik', sans-serif;background-color:#0984e3}.App{text-align:center}.wrapper{height:100vh;width:100%;overflow-x:hidden}.title{font-family:'Yellow Rabbit';color:white;margin:50px 0;font-weight:700;text-align:center;font-size:150px}@font-face{font-family:'Yellow Rabbit';src:local("Yellow Rabbit"),url(/python-code-challenger/static/media/yellow-rabbit.c042a11a.otf) format("opentype")}

.problemContainer{height:100%}.problem{overflow-y:scroll !important;background-color:#f9f9f9;height:100vh;position:relative;padding:20px}.problem .bold{font-weight:700}.problem .hideButton{position:absolute;top:0px;right:10px}.problem li{margin-left:30px}code{background-color:#eee;color:black;padding:1px 3px}.hidden{display:none}

.ActionBox{background-color:#333;height:10vh;display:flex;flex-direction:row;justify-content:space-between;align-items:center;padding:20px}

.terminal{height:40vh;border-radius:0px !important}.terminal-hidden{display:none}.react-console-emulator__input{margin-top:2px}

.CodeEditor{position:relative}.CodeEditor .expandButton{position:absolute;top:10px;right:10px;width:50px;height:50px;border-radius:50%;border:none;z-index:2}

.UnobtrusiveNavbar{position:absolute;z-index:3;top:10px;right:10px;width:40px;height:40px;border:none;border-radius:100%;background-color:white;display:flex;flex-direction:column;justify-content:center;align-items:center}.UnobtrusiveNavbar__menu{position:absolute;top:0;right:0;bottom:0;left:0;z-index:10;display:flex;justify-content:center;align-items:center;flex-direction:column;background-color:#333;width:100vw;height:100vh}.UnobtrusiveNavbar__menu-link{transition:.3s;color:white;text-decoration:none;font-size:30px;padding:10px 30px;border-radius:15px;margin-top:20px}.UnobtrusiveNavbar__menu-link:hover{text-decoration:none;background-color:white;color:black}

.about--img{transition:.3s;border:15px solid white;border-radius:20px;box-shadow:10px 10px 32px -18px rgba(0,0,0,0.75)}.about--img:hover{transform:translateY(-10px);box-shadow:10px 10px 81px -17px rgba(0,0,0,0.75)}.about--h1{font-family:'Yellow Rabbit';font-size:150px;font-weight:700;text-align:center;margin:50px 0;color:white}.about--p{color:white;font-size:22px}.about--content{padding:70px 0}

